* {
    box-sizing: border-box;
}


.cfy-field {
    color: #414141;
    font-family: "Helvetica Neue", Arial, sans-serif;
    font-size: 14px;
    line-height: 1.4;
    margin: 0 0 12px 0;
    width: 100%;
}

.cfy-input {
    outline: none;
    border-radius: 4px;
    border: 1px solid #ddd;
    color: #414141;
    font-size: 14px;
    line-height: 2.14;
    min-height: 33px;
    padding: 0 10px;
    width: 100%;
}

select.cfy-input {
    -moz-appearance:none !important;
    background: transparent url('data:image/gif;base64,R0lGODlhBgAGAKEDAFVVVX9/f9TU1CgmNyH5BAEKAAMALAAAAAAGAAYAAAIODA4hCDKWxlhNvmCnGwUAOw==') right center no-repeat !important;
    background-position: calc(100% - 5px) center !important;
}

.cfy-label {
    color: #414141;
    display: block;
    font-size: 14px;
    font-weight: 600;
    line-height: 24px;
    margin: 10px 0 5px 0;
    max-width: 100%;
}

.cfy-label:empty {
    margin: 0 !important;
    padding: 0 !important;
}

.cfy-message {
    color: #ff4949;
    display: none;
    font-size: 12px;
    margin: 5px 0 0 0;
}

.cfy-additional-label {
    font-size: 10px;
}

.cfy-input-container {
    position: relative;
}

.cfy-cc-image img {
    padding: 3px;
    position: absolute;
    width: 46px;
    height: 32px;
}

.cfy-field--invalid .cfy-input {
    border-color: #ff4949;
}

.cfy-field--invalid .cfy-message {
    display: block;
}

::placeholder {
    color: #ccc;
    font-weight: 200;
}

::-webkit-input-placeholder {
    color: #ccc;
    font-weight: 200;
}

::-moz-placeholder {
    color: #ccc;
    font-weight: 200;
}

:-ms-input-placeholder {
    color: #ccc;
    font-weight: 200;
}

:-moz-placeholder {
    color: #ccc;
    font-weight: 200;
}

.cfy-container--inline {
    background-color: white;
    border-radius: 4px;
    border: 1px solid #ddd;
    white-space: nowrap;
}

.cfy-container--inline .cfy-field {
    display: inline-block;
    margin: 0;
}

.cfy-container--inline .cfy-input {
    border: none;
    padding: 0 7px 0 5px;
}

.cfy-container--inline.cfy-container--invalid {
    border-color: #ff4949;
}

.cfy-container--inline .cfy-field--invalid .cfy-input {
    color: #ff4949;
}

.cfy-container--inline .cfy-field--number {
    max-width: 225px;
}

.cfy-container--inline .cfy-field--month {
    max-width: 42px;
    padding-right: 3px;
}

.cfy-container--inline .cfy-field--month .cfy-input-container::after {
    content: "/";
    position: absolute;
    right: -5px;
    width: 7px;
    padding-top: 8px;
}

.cfy-container--inline .cfy-input--month {
    text-align: right;
}

.cfy-container--inline .cfy-field--year {
    max-width: 56px;
}

.cfy-container--inline .cfy-field--cvv {
    max-width: 48px;
}

.cfy-container--inline .cfy-label,
.cfy-container--inline .cfy-message {
    display: none;
}

.cfy-container--inline .cfy-input--number {
    padding-left: 10px;
}

.cfy-cc-image + .cfy-input.cfy-input--number {
    padding-left: 50px;
}